import axios from "axios";
const downloadUrl = url => {
  let iframe = document.createElement('iframe')
  iframe.style.display = 'none'
  iframe.src = url
  iframe.onload = function () {
    document.body.removeChild(iframe)
  }
  document.body.appendChild(iframe)
}

// 
axios.interceptors.request.use(function (config) {
  // 
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});
// 
axios.interceptors.response.use(function (res) {
  if (res.headers && (res.headers['content-type'] === 'application/x-msdownload' || res.headers['content-type'] === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || res.headers['content-type'] === 'application/zip')) {
    downloadUrl(res.request.responseURL)
    return
  }
  // 
  return res;
}, function (error) {
  switch (error.response.status) {
    case 404:
      //404
      break;
    case 500:
      //500
      break;
    default:
      break;
  }
  alert(":" + error.response.statusText + "\n" +
    ":" + error.response.status)
  // 
  return Promise.reject(error.response.response)
});
export default {
  get(url,options){
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url,
        params:options.params,
        timeout: 30 * 1000,//30
        headers: options.header || {'Content-Type': 'application/json'}
      }).then(response => {
        resolve(response.data)
      }).catch(error => {
        reject()
      })
    })
  },
  post(url,options){
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        data:options.data,
        timeout: 30 * 1000,//30
        headers: options.header || {'Content-Type': 'application/json'}
      }).then(response => {
        resolve(response.data)
      }).catch(error => {
        reject()
      })
    })
  }
}



