<template>
    <div class="index">
        <div class="nav-top">
            <n3-alert dismissable :show="true" icon="smile-o">
                <strong>N3+VUE</strong>
                <div class="nav-dis">
                    
                    <n3-label type="primary">UI: N3</n3-label>
                    <n3-label type="danger">:echarts</n3-label>
                    <n3-label type="success">:axios</n3-label>
                    <n3-label type="info">:file-saver + xlsx</n3-label>
                    <n3-label type="warning">:Vue-Quill-Editor  ueditor</n3-label>
                </div>
                <n3-nav mode="horizontal" :default-active="activeIndex" router>
                    <n3-nav-item index="/doc/home">N3</n3-nav-item>
                    <n3-nav-item index="/doc/echarts">Echarts</n3-nav-item>
                    <n3-nav-item index="/doc/axios">axios</n3-nav-item>
                    <n3-nav-item index="/doc/exportTable"></n3-nav-item>
                    <n3-nav-item index="/doc/editor">: Vue-Quill-Editor</n3-nav-item>
                    <n3-nav-item index="/doc/ueditor">: ueditor</n3-nav-item>
                </n3-nav>
            </n3-alert>

        </div>
        <keep-alive>
            <router-view/>
        </keep-alive>
    </div>
</template>

<script>
    export default {
        name: 'Index',
        data () {
            return {
                activeIndex : this.$route.path
            }
        },
    }
</script>

