<template>
    <div class="datalocking">
        <script id="ueditor" type="text/plain"></script>
        <div class="button-gruop text-center">
            <n3-button type="primary" @click.native="submit"></n3-button>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'DataLocking',
        data () {
            return {
                editor:"",
                config:{
                    initialFrameHeight:500
                },
            }
        },
        mounted(){
            const _this = this;
            this.editor = UE.getEditor('ueditor', this.config); // UE
            this.editor.addListener("ready", function () {
                _this.editor.setContent(_this.defaultMsg); // UE
            });
        },
        methods: {
            handleSelect(){

            },
            getUEContent() { // 
                return this.editor.getContent()
            },
            destroyed() {
                this.editor.destroy();
            },
            submit(){
                this.n3Alert({
                    content: '',
                    type: 'success',
                    placement: 'top-right',
                    duration: 2000,
                    width:'200px' // width
                })
            }
        },
        destroyed () {
            this.destroyed()
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    .datalocking{
        padding-top: 15px;
        .button-gruop{
            margin-top: 15px;
            button{
                margin-right: 15px;
            }
        }
    }

</style>



